* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    outline: none;
    text-decoration: none;
}

.topbar-one {
    background-color: var(--ostech-secondary, #232429);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
}


@media(min-width : 768px) {
    .topbar-one {
        display: block;
    }
}

.topbar-one .container-fluid {
    max-width: 1680px;
}


.top-bar {
    background-color: #222;
    color: white;
    padding: 10px 0;
    font-size: 14px;
}

.top-bar a {
    color: #ddd;
    text-decoration: none;
}

.top-bar a:hover {
    color: #ffc107;
}

.contact-info i {
    color: #ffc107;
    margin-right: 5px;
}

.social-icons a {
    margin-left: 10px;
    color: white;
}

.social-icons a:hover {
    color: #ffc107;
}

.phone-section {
    background-color: #ffa500;
    padding: 10px 15px;
    border-radius: 5px;
}




.topbar-one__info {
    display: flex;
    align-items: center;
    margin: 0;
}

@media(max-width:1800px) {
    .topbar-one__info {

        margin-left: 20px;
    }
}

.topbar-one__info__item {
    display: flex;
    align-items: center;
    color: var(--ostech-text-gray, #999B9F);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 128.571%;
}

.topbar-one__info__item a {
    color: inherit;
    background: linear-gradient(to right, currentcolor 0%, currentcolor 100%) 0px 95%/0px 1px no-repeat;
    transition: all 500ms ease;
}

.topbar-one__info__item a:hover {
    background-size: 100% 1px;
}

.topbar-one__info__item a:hover {
    color: var(--ostech-white, #fff);
}

.topbar-one__info__item+.topbar-one__info__item {
    margin-left: 50px;
}

.topbar-one__info__icon {
    font-size: 12px;
    color: var(--ostech-base, #EE8624);
    position: relative;
    top: 2px;
    margin-right: 9px;
}

.topbar-one__right {
    padding-top: 12px;
    padding-bottom: 12px;
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    margin-top: 10px;
}

@media (min-width: 992px) {
    .topbar-one__right {
        margin-top: 0;
        margin-left: auto;
    }
}

.topbar-one__right__call {
    display: flex;
    align-items: center;
    gap: 10px;
    padding-right: 20px;
    margin-right: 20px;
    border-right: 1px solid rgba(var(--ostech-secondary-rgb, 35, 36, 41), 0.4);
}

.topbar-one__right__call__icon {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: var(--ostech-gray, #F4F5F8);
    color: var(--ostech-secondary, #232429);
    font-size: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.4s ease-in-out;
}

.topbar-one__right__call__text {
    margin-bottom: 0;
    padding-bottom: 0;
    color: var(--ostech-text-dark, #222429);
    font-size: 16px;
    font-weight: 600;
    line-height: 187.5%;
    background: linear-gradient(to right, currentcolor 0%, currentcolor 100%) 0px 95%/0px 1px no-repeat;
    transition: all 500ms ease;
}

.topbar-one__right__call__text:hover {
    background-size: 100% 1px;
}

.topbar-one__right__call:hover .topbar-one__right__call__icon {
    background: var(--ostech-white, #fff);
    color: var(--ostech-base, #EE8624);
}




