.topbar-one__right::before {
    content: "";
    background: var(--ostech-base, #EE8624);
    z-index: -1;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 90%;
}

.topbar-one__right::after {
    content: "";
    background: #EE8624;
    z-index: -1;
    width: 95%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

